.form-section {
  width: 100%;
  background-color: white;
  position: relative;
  max-width: 600px;
  border-radius: 5px;
  
}
form {
  margin-top: 150px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
label {
  font-size: 13px;
  color: grey;
}
input {
  width: 100%;
  outline: none;
  border: 1px solid rgb(171, 158, 158);
  height: 30px;
  border-radius: 10px;
  padding-left: 10px;
}
.additional-info {
  display: flex;
  gap: 15px;
}
button {
  height: 40px;
  border: none;
  border-radius: 10px;
  background: linear-gradient(45deg, blueviolet, deeppink);
  color: white;
  font-size: 15px;
  font-weight: 700;
  cursor: pointer;

}
button:hover{
  opacity: 0.8;
}
.error-message{
  color: red;
  font-size: 12px;
  margin-top: 2px;
  text-align: right;
  font-style: italic;
}
.error{
  border: 1px solid red;
}

